.account-container{
    width: 300px;
    height: 100%;
    background-color: green;
    color: blue;
    z-index: 7;
    position: absolute;
    right: 0px;
    transition: right to left;
    animation: side-left 2s;
}

@keyframes side-left {
    from {width:0px}
    to {width:300px}
}